@import "../../style.scss";

.comments{
    @include themify($themes){
        img{
            width: 30px;
            height: 30px;
            border-radius: 50%;
            object-fit: cover;
        }

        .write{
          display: flex;
          align-items: center; 
          justify-content: space-between;
          gap: 20px;
          margin: 20px 0px ; 

          input{
            flex: 5;
            padding: 10px;
            border: 1px solid themed("border");
            background-color: transparent;
            color: themed("textColor");
          }

          button{
            border: none;
            background-color: #5271ff;
            color: white;
            padding: 10px;
            cursor: pointer;
            border-radius: 3px;
          }
        }

        .comment{
            margin: 30px 0px;
            display: flex;
            justify-content: space-between; //horizental line
            gap: 20px;

            .info{
                flex: 5;
                display: flex;
                flex-direction: column;
                gap: 3px;
                align-items: flex-start; //no center align left

                span{
                    font-weight: 500;
                }

                p{
                color: themed("textColorSoft"); 
                }
            }

            .date{
                flex: 1;
                align-self: center;
                color: gray;
                font-size: 12px;
            }
        }
    }
}