@import "../../style.scss";

.post{
    @include themify($themes){ //background create

         //Box shadow
         -webkit-box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.38);
         -moz-box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.38);
         box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.38);
         border-radius: 20px;
         //padding: 10px;
         //margin-bottom: 10px;
         background-color: themed("bg");
         color: themed("textColor");

    .container{
        padding: 20px;

        .user{
            display: flex;
            align-items: center;
            justify-content: space-between;

            .userInfo{
                display: flex;
                gap: 20px;

                img{
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                    object-fit: cover;
                  }
                 .details{
                    display: flex;
                    flex-direction: column;
                    cursor: pointer;

                    .name{
                        font-weight: 500;
                    }
                    .date{
                        font-size: 12px;
                    }

                }
            }
        }

        .content{
            margin: 20px 0px;

            img{
                width: 100%;
                max-height: 500px;
                object-fit: cover;
                margin-top: 20px;
            }
        }

        .info{
           display: flex; 
           align-items: center;
           gap: 20px;

           .item{
            display: flex;
            align-items: center;
            gap: 10px;
            cursor: pointer;
            font-size: 14px;
           }
        }
    }
}
}